body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.auth-container{
    background-color: #179168 ;
    background-repeat: no-repeat;
    background-position: 5%;
    height: 100vh;
    width: 100vw;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
}

.body-form {
    background: #f6f5f7;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 180vh;
}

.h1-form {
    font-weight: bold;
	margin-top: 25px;
    margin-right: 95px;
}

.p-form {
    font-size: 14px;
    font-weight: 400;
    line-height: 20px;
    letter-spacing: .5px;
    margin: 20px 0 30px;
}

.a-form {
    color: #333;
    font-size: 14px;
    text-decoration: none;
    margin: 15px 0;
}

.container-form {
    background: #fff;
    border-radius: 30px;
    
    box-shadow: 0 14px 28px rgba(0, 0, 0, .2), 0 10px 10px rgba(0, 0, 0, .2);
    position: relative;
    overflow: hidden;
    width: 550px;
    min-height: 600px;
}

.form-container {
    background: #fff;
    display: flex;
    flex-direction: column;
    padding:  10px 0 50px;
    height: 100%;
    justify-content: center;
    align-items: center;
    text-align: center;
}


.form-container input {
    background: #eee;
    border: none;
    padding: 12px 15px;
    margin: 20px 0;
    width: 100%;
}

button:active {
    transform: scale(.95);
}

.form-container {
    position: absolute;
    top: 0;
    height: 100%;
    transition: all .6s ease-in-out;
}

.sign-in-container {
    margin-left: 30px;
}

.sign-up-container {
    left: 0;
    width: 50%;
    z-index: 1;
    opacity: 0;
}

